import React, {useState, useEffect} from 'react';
import {Button, Card, Col, Form, Input, message, Row, Select, Space, TimePicker} from 'antd'
import AirlinesSelectForm from "../../../../../components/forms/airlines.select.form";
import CitySelectForm from "../../../../../components/forms/city-select.form";
import TypeStatusSelectForm from "../../../../../components/forms/type-status.select.form";
import moment from "moment";
import Utils, {findKey} from "../../../../../../../utils";
import Flex from "../../../../../../../components/shared-components/Flex";
/**
 *
 * @param {object} props
 * @param {'CREATE' | 'UPDATE'} props.mode
 * @param {object | Array | []} props.initial
 * @returns {JSX.Element}
 */
function CounterForm(props) {
    let { mode,initial,form } = props
    const [ identifier,setIdentifier] = useState('')
    
    useEffect(()=> {
        if(mode === 'UPDATE'){
            
            let field = {}
            if(typeof(initial) === 'object' && initial && Object.keys(initial).length > 0){
                Object.keys(initial).forEach((key)=> {
                    if(typeof(initial[key]) === "object" && initial[key] && Object.keys(initial[key]).length > 0){
                        Object.keys(initial[key]).forEach((k)=> {
                            if(k === 'start_time' || k === 'end_time'){
                                Reflect.set(initial[key],k,moment(initial[key][k],'HH:mm'))
                            }
                        })
                    }
                    Reflect.set(field,key,initial[key])
                })
            }
            form.setFieldsValue({
                identifier: field?.identifier ?? null,
                data: {
                    ...field?.data
                }
            });
            
        }
    },[mode,initial,form])
    
    
    return (
      <React.Fragment>
          <Form
            form={form}
            layout={'vertical'}
            initialValue={{
                ...initial
            }}
          >
              <Row gutter={24} justify={'center'}>
                  <Col xs={24} lg={mode === 'UPDATE' ? 24:16}>
                      {
                          mode === 'UPDATE'? <Row gutter={24}>
                              <Col lg={12} xs={24}>
                                  <Form.Item name={['data', 'flight_no']} label={'Flight Number'} hasFeedback
                                             rules={[{required: true, message: "Can't be empty"}]}>
                                      <Input autoComplete={'off'} placeholder={'Input your identifir'}/>
                                  </Form.Item>
                              </Col>
                              <Col lg={12} xs={24}>
                                  <Form.Item name={['data', 'name']} label={'Display Name'} hasFeedback
                                             rules={[{required: true, message: "Can't be empty"}]}>
                                      <Input autoComplete={'off'} placeholder={'Input your identifir'}/>
                                  </Form.Item>
                              </Col>
                              
                              <Col lg={12} xs={24}>
                                  <Form.Item name={['data', 'start_time']} label={'Start Time'} hasFeedback
                                             rules={[{required: true, message: "Can't be empty"}]}>
                                      <TimePicker
                                        style={{width: "100%"}}
                                        format={'HH:mm'}
                                      />
                                  </Form.Item>
                              </Col>
                              <Col lg={12} xs={24}>
                                  <Form.Item name={['data', 'end_time']} label={'End Time'} hasFeedback
                                             rules={[{required: true, message: "Can't be empty"}]}>
                                      <TimePicker
                                        type={'time'}
                                        style={{width: "100%"}}
                                        format={'HH:mm'}
                                      />
                                  </Form.Item>
                              </Col>
                              <Col lg={8} xs={24}>
                                  <Form.Item name={['data','airline','id']} hidden>
                                      <Input/>
                                  </Form.Item>
                                  <AirlinesSelectForm
                                    name={['data', 'airline','name']}
                                    label={'Airlines'}
                                    select={{
                                        onChange: function(value,args) {
                                            console.log({value,args})
                                            form.setFieldsValue({
                                                data: {
                                                    airline:{
                                                        name: args?.item?.data?.name ?? null,
                                                        id:value
                                                    }
                                                }
                                            })
                                        }
                                    }}
                                    rules={[{required: true, message: "can't be empty!"}]}
                                    keyValue={['id']}
                                    keyDisplay={['data','name']}
                                  >
                                      {({item,index})=> {
                                          return (
                                            <Select.Option value={item?.id} item={item} key={item?.id}>{findKey(item,['data','name']) ?? "undefined"}</Select.Option>
                                          )
                                      }}
                                  </AirlinesSelectForm>
                              </Col>
                              <Col lg={8} xs={24}>
                                  <Form.Item name={['data','city','id']} hidden>
                                      <Input/>
                                  </Form.Item>
                                  <CitySelectForm
                                    name={['data', 'city','name']}
                                    label={'City'}
                                    select={{
                                        onChange: function(value,args) {
                                            console.log({value,args})
                                            form.setFieldsValue({
                                                data: {
                                                    city:{
                                                        name: args?.item?.data?.name ?? null,
                                                        id:value
                                                    }
                                                }
                                            })
                                        }
                                    }}
                                    rules={[{required: true, message: "can't be empty!"}]}
                                    keyValue={'id'}
                                    displayValue={['data','name']}
                                  >
                                      {({item,index})=> {
                                          return (
                                            <Select.Option value={item?.id} item={item} key={item?.id}>{findKey(item,['data','name']) ?? "undefined"}</Select.Option>
                                          )
                                      }}
                                  </CitySelectForm>
                              </Col>
                              <Col lg={8} xs={24}>
                                  <TypeStatusSelectForm
                                    name={['data', 'status']}
                                    label={'Status'}
                                    rules={[{required: true, message: "can't be empty!"}]}
                                    keyValue={['data','name']}
                                  />
                              </Col>
                          
                          
                          </Row>
                            :
                            <Card bordered={false}>
                                <Row gutter={24}>
                                    <Col lg={12} xs={24}>
                                        <Form.Item name={['data', 'flight_no']} label={'Flight Number'} hasFeedback
                                                   rules={[{required: true, message: "Can't be empty"}]}>
                                            <Input autoComplete={'off'} placeholder={'Input your identifir'}/>
                                        </Form.Item>
                                    </Col>
                                    <Col lg={12} xs={24}>
                                        <Form.Item name={['data', 'name']} label={'Display Name'} hasFeedback
                                                   rules={[{required: true, message: "Can't be empty"}]}>
                                            <Input autoComplete={'off'} placeholder={'Input your identifir'}/>
                                        </Form.Item>
                                    </Col>
                                    
                                    <Col lg={12} xs={24}>
                                        <Form.Item name={['data', 'start_time']} label={'Start Time'} hasFeedback
                                                   rules={[{required: true, message: "Can't be empty"}]}>
                                            <TimePicker
                                              style={{width: "100%"}}
                                              format={'HH:mm'}
                                            />
                                        </Form.Item>
                                    </Col>
                                    <Col lg={12} xs={24}>
                                        <Form.Item name={['data', 'end_time']} label={'End Time'} hasFeedback
                                                   rules={[{required: true, message: "Can't be empty"}]}>
                                            <TimePicker
                                              type={'time'}
                                              style={{width: "100%"}}
                                              format={'HH:mm'}
                                            />
                                        </Form.Item>
                                    </Col>
                                    <Col lg={8} xs={24}>
                                        <Form.Item name={['data','airline','id']} hidden>
                                            <Input/>
                                        </Form.Item>
                                        <AirlinesSelectForm
                                          name={['data', 'airline','name']}
                                          label={'Airlines'}
                                          select={{
                                              onChange: function(value,args) {
                                                  console.log({value,args})
                                                  form.setFieldsValue({
                                                      data: {
                                                          airline:{
                                                              name: args?.item?.data?.name ?? null,
                                                              id:value
                                                          }
                                                      }
                                                  })
                                              }
                                          }}
                                          rules={[{required: true, message: "can't be empty!"}]}
                                          keyValue={['id']}
                                          keyDisplay={['data','name']}
                                        >
                                            {({item,index})=> {
                                                return (
                                                  <Select.Option value={item?.id} item={item} key={item?.id}>{findKey(item,['data','name']) ?? "undefined"}</Select.Option>
                                                )
                                            }}
                                        </AirlinesSelectForm>
                                    </Col>
                                    <Col lg={8} xs={24}>
                                        <Form.Item name={['data','city','id']} hidden>
                                            <Input/>
                                        </Form.Item>
                                        <CitySelectForm
                                          name={['data', 'city','name']}
                                          label={'City'}
                                          select={{
                                              onChange: function(value,args) {
                                                  console.log({value,args})
                                                  form.setFieldsValue({
                                                      data: {
                                                          city:{
                                                              name: args?.item?.data?.name ?? null,
                                                              id:value
                                                          }
                                                      }
                                                  })
                                              }
                                          }}
                                          rules={[{required: true, message: "can't be empty!"}]}
                                          keyValue={'id'}
                                          displayValue={['data','name']}
                                        >
                                            {({item,index})=> {
                                                return (
                                                  <Select.Option value={item?.id} item={item} key={item?.id}>{findKey(item,['data','name']) ?? "undefined"}</Select.Option>
                                                )
                                            }}
                                        </CitySelectForm>
                                    </Col>
                                    <Col lg={8} xs={24}>
                                        <TypeStatusSelectForm
                                          name={['data', 'status']}
                                          label={'Status'}
                                          rules={[{required: true, message: "can't be empty!"}]}
                                          keyValue={['data','name']}
                                        />
                                    </Col>
                                
                                
                                </Row>
                            </Card>
                      }
                  </Col>
              </Row>
          
          </Form>
      </React.Fragment>
    );
}

export default CounterForm;