import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {
	Button,
	Tooltip,
	Card,
	Col,
	DatePicker,
	Drawer,
	Form,
	Input,
	message,
	Modal,
	Row,
	Select,
	Table,
	Divider
} from "antd";
import {
	DeleteOutlined,
	EditOutlined, PlusOutlined,
	ReloadOutlined, SaveOutlined, SettingOutlined
} from "@ant-design/icons";
import {useHistory} from 'react-router-dom'
import ApiService from "services/api.service";
import moment from "moment/moment";
import TypeStatusAirlinesForm from "../../../../../components/forms/type-status-airlines.form";
import AirlinesSelectForm from "../../../../../components/forms/airlines.select.form";
import CitySelectForm from "../../../../../components/forms/city-select.form";
import CounterForm from "../components/counter.form";
import ButtonPermission from "../../../../../../../components/permission-components/button.permission";

function mapStateToProps(state) {
	return {};
}

function Index() {
	
	const history = useHistory();
	
	const defaultColumns = [
		{
			visible:true,
			key:"no",
			title:"No",
			dataIndex: "id",
			width: 70,
			render: (_, val, index) => ((index + 1) + (params?.limit * (params?.page - 1)))
		},
		{
			visible:true,
			key:"display_name",
			dataIndex: ['data', 'name'],
			title: "Display Name",
			render: (_) => _ ?? "-"
		},
		{
			visible:true,
			key:"Airlines",
			dataIndex: ['data', 'airline','name'],
			title: "Airline",
			render: (_) => _ ?? "-"
		},
		{
			visible:true,
			key:"flight_no",
			dataIndex: ['data', 'flight_no'],
			title: "Flight No",
			render: (_) => _ ?? "-"
		},
		{
			visible:true,
			key:"to-or-from",
			dataIndex: ['data', 'city','name'],
			title: "To / From",
			render: (_) => _ ?? "-"
		},
		{
			visible:true,
			key:"start_time",
			dataIndex: ['data', 'start_time'],
			title: "Start Time",
			
			render: (_) => {
				if(moment(_).isValid()){
					return moment(_).format("HH:mm")
				}
				return _
			}
		},
		{
			visible:true,
			key:"end_time",
			dataIndex: ['data', 'end_time'],
			title: "End Time",
			render: (_) => {
				if(moment(_).isValid()){
					return moment(_).format("HH:mm")
				}
				return _
			}
		},
		{
			visible:true,
			key:"Status",
			dataIndex: ['data', 'status'],
			title: "Status",
			render: (_) => _ ?? "-"
		},
		{
			visible:true,
			key: 'action',
			width: 120,
			dataIndex: 'id',
			render: (_, val, index) => {
				return (
					<div className={'text-right d-flex !mk-gap-2'}>
						<Button size={'small'} type={'primary'} ghost icon={<EditOutlined/>}
						        onClick={() => handleClickEdit(val, index)}/>
						<Button
							size={'small'}
							type={'primary'}
							danger
							ghost
							onClick={() => {
								handleDeleteRow(_, index)
							}}
							loading={loadings[index] ?? false}
							icon={<DeleteOutlined/>}/>
					</div>
				)
			}
		},
	]
	const [loading, setLoading] = useState(true)
	const [params, setParams] = useState({
		page: 1,
		limit: 10
	})
	const [pagination, setPagination] = useState({
		page: 1,
		limit: 10
	})
	const [data, setData] = useState([])
	
	const [loadings, setLoadings] = useState([])
	const [visibleDrawer, setVisibleDrawer] = useState(false)
	const [visible, setVisible] = useState(false)
	const [selected, setSelected] = useState(null)
	const [form] = Form.useForm();
	
	function _caller() {
		setLoading(true)
		new ApiService({
			url: "/api/v1/master-data/counter",
			config: {
				params: {
					...params
				}
			}
		})
			.get()
			.then((response) => {
				response.data = response.data.map((item) => ({
					...item,
					schedule_time: moment(item?.schedule_time, 'HH:mm').isValid() ? moment(item?.schedule_time, 'HH:mm') : item?.schedule_time
				}))
				setData([...response?.data])
				setPagination({
					...pagination,
					...response?.pagination,
					...response?.paginate
				})
				setLoading(false)
			})
			.catch((err) => {
				setData([])
				setLoading(false)
			})
	}
	
	
	useEffect(() => {
		_caller()
	}, [params])
	
	useEffect(() => {
	}, [visible])
	
	const enterLoading = (index, value = true) => {
		setLoadings((prevLoadings) => {
			prevLoadings[index] = value;
			return prevLoadings ?? []
		});
	};
	
	const onFinish = () => {
		form.validateFields()
			.then((value) => {
				Reflect.set(value, 'schedule_time', moment(value?.schedule_time).isValid() ? moment(value?.schedule_time).format("HH:mm") : value?.schedule_time)
				new ApiService({
					body: value,
					url: `/api/v1/master-data/counter/${selected?.id}`,
				}).post()
					.then((response) => {
						message.success({
							content: response?.message ?? "Successfully created",
							key: "create-city",
							duration: 2
						})
						setLoading(false)
					})
					.catch((err) => {
						message.info(JSON.stringify(err))
						message.error({
							content: err?.message ?? "Error: Some Error",
							key: "create-city",
							duration: 2
						})
						setLoading(false)
					})
			})
			.catch((err) => {
			
			})
	}
	
	function handleClickEdit(_, index) {
		setVisible(!visible)
		setSelected(_)
	}
	
	function onCloseModal() {
		form.resetFields()
		setVisible(false)
		setSelected(null)
		
	}
	
	
	function handleDeleteRow(_, index) {
		enterLoading(index, true)
		new ApiService({
			url: `/api/v1/master-data/counter/${_}`,
		}).delete()
			.then((response) => {
				if (response?.response) {
					if (data && typeof (data[index]) !== 'undefined') {
						data[index].isDeleted = true
						setData([...data])
						enterLoading(index, false)
					}
					message.success(response?.message ?? "Successfully deleted")
					setTimeout(() => {
						setData([...data.filter((item) => item.id !== _)])
						setLoadings([])
					}, 2000)
				} else {
					enterLoading(index, false)
					setLoadings([])
					message.error(response?.message ?? "Error: delete by id " + _)
				}
				
			})
			.catch((err) => {
				enterLoading(index, false)
				setLoadings([])
			})
	}
	
	const onChangeDrawer = (val) => {
		setVisibleDrawer(val ?? false)
	}
	
	
	const extra = (
		<div className={'d-flex align-items-center mk-gap-2'}>
			<Tooltip title={'Reload Data'}>
				<Button icon={<ReloadOutlined/>} ghost type={'primary'} onClick={_caller} loading={loading} size={'small'}/>
			</Tooltip>
			<Tooltip title={'Setting'}>
				<Button icon={<SettingOutlined/>} type={'primary'} ghost onClick={() => onChangeDrawer(true)} size={'small'}/>
			</Tooltip>
		</div>
	)
	
	const [columns, setColumns] = useState(defaultColumns ?? [])
	
	const onColumnsChange = (value) => {
		setColumns(() => {
			return defaultColumns.filter((item) => value.includes(item.key))
		})
	}
	
	return (
		<React.Fragment>
			<Drawer
				title={`Filter`}
				placement={"right"}
				closable
				onClose={() => onChangeDrawer(false)}
				visible={visibleDrawer}>
				<div className={'w-100'}>
					<h5>Table Column</h5>
					<Select onChange={onColumnsChange} mode={'tags'} className={'w-100'} placeholder={'Select Columns Active'}
					        defaultValue={defaultColumns.filter((item)=> item.visible === true).map((item) => item?.key) ?? []}>
						{
							defaultColumns.map((item) => (
								<Select.Option value={item.key} key={item.key}>{`${item.key}`.replace(/_/g, ' ')}</Select.Option>
							))
						}
					</Select>
					<Divider/>
				</div>
			</Drawer>
			
			<Modal
				style={{width: "520px !important"}}
				title={'Edit Gate'}
				visible={visible} onOk={onFinish} onCancel={onCloseModal}>
				<CounterForm
					form={form}
					visible={visible}
					mode={'UPDATE'}
					initial={{
						...selected,
						data: selected?.data ?? {}
					}}
				/>
			</Modal>
			<HeroComponents
				loading={false}
				name={"Overview"}
				positionName={['Master Data', 'Counter']}
				thumbnail={'/overview.png'}
				extra={<div className={'text-right'}>
					<Button type={'primary'} size={'small'} icon={<PlusOutlined/>} ghost onClick={()=> history.push('/dashboard/master-data/counter/create')}>Add New</Button>
        </div>}
			/>
			<div style={{marginTop: 90}}>
				<ContainerTailwind>
					<Card bordered={false} extra={extra}>
						<Table
							loading={loading}
							dataSource={data}
							rowClassName={(record, index) => record?.isDeleted ? "!mk-bg-red-500 !mk-bg-opacity-20" : ''}
							columns={columns}
							pagination={{
								pageSize: params?.limit,
								current: params?.page,
								total: pagination?.total_items ?? 0,
								onChange: function (page, pageSize) {
									setParams({
										...params,
										page,
										limit: pageSize
									})
								}
							}}
						/>
					</Card>
				</ContainerTailwind>
			</div>
		</React.Fragment>
	);
}

export default connect(
	mapStateToProps,
)(Index);